<template>
    <div v-if="!changepw" class="password-reset-container">
        <div class="password-reset-form">
            <h2 class="form-title"> </h2>
            <div class="form-group">
                <label for="email"></label>
                <input type="email" id="email" v-model="email" placeholder=" ">
            </div>
            <div v-if="!isCodeSent">
                <button class="btn btn-primary" @click="sendCode"> </button>
            </div>
            <div v-else>
                <input type="email" id="code" v-model="code" placeholder=""/>
                <button class="btn-primary1" @click="Verify"></button>
                <button class="btn-primary1" @click="sendCode"></button>
            </div>
        </div>
    </div>
    <div class="black-bg" v-else>
      <div class="white-bg">
        <div class="title">  </div>
        <div class="title"><input class="pw" type="password" v-model="newPw"></div>
        <div class="title"> <input class="pw" type="password" v-model="newPwcheck"></div>
        <label for=""><div v-if="!passwordMatch">  .</div></label>
        <div class="title" style="align-items: center; display: flex;">
            <button @click="Changepw = false"></button>
            <button @click="changePw"></button>
        </div>
     </div>
    </div>
</template>

<script>
import http from '@/api/http'
export default {
  data () {
    return {
      email: '',
      code: '',
      IsVerify: false,
      isCodeSent: false,
      verifycode: null,
      changepw: false,
      newPw: '',
      newPwcheck: ''
    }
  },
  methods: {
    sendCode () {
      const data = {
        email: this.email
      }
      const emailRegex = /\b[A-Z0-9._%+-]+@mju\.ac\.kr\b/i
      if (!this.email) {
        alert(' ')
      } else if (!emailRegex.test(this.email)) {
        alert('   .  @mju.ac.kr  .')
      } else {
        http.post('/email', data)
          .then(res => {
            this.isCodeSent = true
            this.verifycode = res.data
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    Verify () {
      if (this.code === this.verifycode) {
        alert('.')
        this.changepw = true
      } else {
        alert(' .')
      }
    },
    changePw () {
      const data = {
        loginId: this.email,
        newPassword: this.newPw,
        checkPassword: this.newPwcheck
      }
      http.post('/password_change', data)
        .then(response => {
          alert('  ')
          this.changepw = false
          this.$router.push('/LoginCheck')
        })
        .catch(error => {
          alert('  .')
          location.reload()
          console.log(error)
        })
    }
  },
  computed: {
    passwordMatch () {
      return this.newPw === this.newPwcheck
    }
  }
}
</script>

